<template>
  <div class="zd-common-component">
    <Form class="zd-cm-applyForm" ref="provideForm" :model="provideForm" :rules="ruleValidate" :label-width="80" inline>
      <!--<FormItem label="出库单号：" prop="outOrder">
        <Input v-model.trim="provideForm.outOrder" placeholder="出库单号" class="zd-common-w200" :maxlength="30"></Input>
      </FormItem>-->
      <FormItem label="出库日期：" prop="date">
        <DatePicker v-model="provideForm.date" :editable="false" type="date" format="yyyy-MM-dd" placeholder="出库日期" class="zd-common-input-h38 zd-common-w150 zd-common-mr10"></DatePicker>
      </FormItem>
      <FormItem label="出库仓库：">
        <Input class="zd-common-input-h38 zd-common-w150 zd-common-mr10" v-model.trim="provideForm.warehouseName" readonly></Input>
        <!--<Select v-model="provideForm.warehouse" placeholder="选择出库仓库" class="zd-common-w150 zd-common-mr10">
          <Option v-for="item in warehouseArr" :value="item.id" :key="item.id">{{ item.name }}</Option>
        </Select>-->
      </FormItem>
      <FormItem label="出库类型：" prop="type">
        <Select v-model="provideForm.type" placeholder="选择出库类型" class="zd-common-input-h38 zd-common-w150 zd-common-mr10">
          <Option v-for="item in typeArr" :value="item.code" :key="item.code">{{ item.name }}</Option>
        </Select>
      </FormItem>
      <FormItem label="部门：" prop="department" :label-width="40">
        <Select v-model="provideForm.department" placeholder="选择领用部门" class="zd-common-input-h38 zd-common-w200 zd-common-mr10">
          <Option :value="item.id" v-for="item in departmentArr" :key="item.id">{{ item.name }}</Option>
        </Select>
      </FormItem>
      <FormItem label="领用人：" prop="leader" :label-width="50">
        <Select
          ref="leader"
          class="zd-common-input-h38 zd-common-w150 zd-common-fl"
          placeholder="请输入领用人"
          v-model="provideForm.leader"
          filterable
          remote
          :remote-method="remoteMethod"
          :loading="loading">
          <Option v-for="option in leaderArr" :value="option.id" :key="option.id">{{option.name}}</Option>
        </Select>
      </FormItem>
    </Form>
    <div class="zd-common-black-b zd-common-pt5 zd-common-pb5 zd-common-pl20">
      <!--<Button type="default" class="zd-common-mr10 zd-common-h32 zd-common-lh8" @click="addGoodsHandle">
        添加
      </Button>-->
      <Button type="default" class="zd-common-mr10 zd-common-h32 zd-common-lh8" @click="delRowHandle">
        删除
      </Button>
      <!--<Input v-model="code" placeholder="请输入物品编码，按Enter添加" :maxlength="50" class="zd-cm-w220 zd-common-input-h32"
             @keyup.enter.native="getGoodsByCodeHandle"/>-->
    </div>
    <Form ref="formGoodsData" :model="formGoodsData" class="zd-common-mt10">
      <Table class="zd-common-overflow-v" border :columns="goodsCols" :data="formGoodsData.attrData" show-summary :summary-method="handleSummary" :row-class-name="rowClassName" @on-row-click="clickRowHandle">
        <template slot-scope="{ row,index }" slot="number">
          <Form-item class="zd-common-mt24" :prop="'attrData.' + index + '.number'"
                     :rules="{required: true, message:'不能为空', trigger:'blur',type:'number'}" v-if="formGoodsData.attrData[index].currentInventory>0">
            <InputNumber :max="formGoodsData.attrData[index].currentInventory" :min="1" :precision='0' placeholder="数量" v-model="formGoodsData.attrData[index].number" @on-change="getCurrIndex(index)"/>
          </Form-item>
          <Form-item class="zd-common-mt24" :prop="'attrData.' + index + '.number'"
                     :rules="{required: true, message:'不能为空', trigger:'blur',type:'number'}" v-else>
            <InputNumber v-model="formGoodsData.attrData[index].number" readonly/>
          </Form-item>
        </template>
        <template slot-scope="{ row,index }" slot="amount">
          <!--<Form-item class="zd-common-mt24" :prop="'attrData.' + index + '.amount'"
                     :rules="{required: true, message:'不能为空', trigger:'blur', type:'number'}">
            <InputNumber :max="100" :min="1" :precision='2' placeholder="金额" v-model="formGoodsData.attrData[index].amount"/>
          </Form-item>-->
          {{ formGoodsData.attrData[index].amount }}
        </template>
      </Table>
    </Form>
    <div class="zd-common-mt10 zd-common-clearFix">
      <span class="zd-common-fl">制单人：{{ userName }}</span>
      <span class="zd-common-fl zd-common-ml20">制单日期：{{ date }}</span>
    </div>
    <Form class="zd-common-mt20" ref="provideRemarkForm" :model="provideRemarkForm">
      <FormItem label="备注：" class="zd-common-mb0">
        <Input style="width: 826px;" class="zd-common-fl" v-model.trim="provideRemarkForm.remark" placeholder="备注" :maxlength="100" type="textarea" :autosize="{minRows: 5,maxRows: 5}"></Input>
      </FormItem>
    </Form>
    <div slot="footer" class="zd-common-modal-footer zd-common-pt30">
      <Button type="primary" class="zd-common-mr35 zd-common-w100 zd-common-h32 zd-common-lh8" @click="handleSubmit"
              :loading="saveLoading">
        <span v-if="!saveLoading">确定</span>
        <span v-else>正在保存...</span>
      </Button>
      <Button @click="toOut" class="zd-common-w100 zd-common-h32 zd-common-lh8">取消</Button>
    </div>

    <!--添加物品-->
    <!--<Modal v-model="addGoodsModal" :mask-closable="false" :width="1200" >
      <h3 slot="header" class="zd-common-modal-header">
        <span>物品档案</span>
      </h3>
      <add-goods ref="child"></add-goods>
      <div slot="footer" class="zd-common-modal-footer zd-common-pt10">
        <div slot="footer" class="zd-common-modal-footer zd-common-pt10">
          <Button type="primary" class="zd-common-mr35 zd-common-w100 zd-common-h32 zd-common-lh8" @click="saveGoodsHandle">确定</Button>
          <Button @click="addGoodsModal = false" class="zd-common-w100 zd-common-h32 zd-common-lh8">取消</Button>
        </div>
      </div>
    </Modal>
    -->
    <!--库存-->
    <Modal v-model="inventoryModal" :mask-closable="false" :width="920" @on-cancel="cancelInventoryModal">
      <h3 slot="header" class="zd-common-modal-header">
        <span>{{ goodsName }}</span>
      </h3>
      <div class="zd-common-tr">
        <i class="zd-common-fs12 zd-common-font-style zd-common-des-font-c">系统默认按照先入先出的原则自动分配出库数量</i>
      </div>
      <div class="zd-common-h400 zd-common-mt10">
        <vue-scroll>
          <Form ref="inventoryData" :model="inventoryData">
            <Table class="zd-common-overflow-v zd-cm-out-table" border :columns="inventoryCols" :data="inventoryData.attrData" show-summary :summary-method="handleInventorySummary">
              <template slot-scope="{ row,index }" slot="outNum" v-if="inventoryData.attrData[index].surplusNum>0">
                <Form-item class="zd-common-mt24">
                  <InputNumber :max="inventoryData.attrData[index].surplusNum" :min="1" :precision='0' placeholder="数量" v-model="inventoryData.attrData[index].outNum" @on-change="calcAmount(index)"/>
                </Form-item>
              </template>
              <template slot-scope="{ row,index }" slot="outNum" v-else>
                <Form-item class="zd-common-mt24">
                  <InputNumber v-model="inventoryData.attrData[index].outNum" readonly/>
                </Form-item>
              </template>
              <template slot-scope="{ row,index }" slot="amount">
                {{ inventoryData.attrData[index].amount }}
              </template>
            </Table>
          </Form>
        </vue-scroll>
      </div>
      <div slot="footer" class="zd-common-modal-footer zd-common-pt10">
        <Button type="primary" class="zd-common-mr35 zd-common-w100 zd-common-h32 zd-common-lh8" @click="confirmInventoryModal">确定</Button>
        <Button @click="cancelInventoryModal" class="zd-common-w100 zd-common-h32 zd-common-lh8">取消</Button>
      </div>
    </Modal>

  </div>
</template>
<script>
  import AddGoods from "../../components/addGoods"
  export default {
    components:{
      AddGoods
    },
    data() {
      return {
        //添加出库单
        warehouseArr:[],
        typeArr:[],
        departmentArr:[],
        provideModal:false,
        provideForm:{
          date:new Date().toLocaleDateString(),
          warehouse:'',
          warehouseName:'',
          type:'',
          department:'',
          leader:''
        },
        applyDepartArr:[],
        goodsCols:[
          {
            type: 'index',
            title:'序号',
            align: 'center',
            width:65,
            key:'sortNum'
          },
          {
            title: '物品名称',
            align: 'center',
            key: 'name',
          },
          {
            title: '物品编码',
            align: 'center',
            key: 'code',
          },
          {
            title: '物品分类',
            align: 'center',
            key: 'classify',
          },
          {
            title: '品牌',
            align: 'center',
            key: 'brand',
          },
          {
            title: '规格/型号',
            align: 'center',
            key: 'model',
          },
          {
            title: '单位',
            align: 'center',
            key: 'unit',
          },
          {
            title: '当前库存',
            align: 'center',
            key: 'currentInventory',
            render:(h,params)=>{
              return h('div',[
                h('span', {
                  class:'zd-common-primary-c zd-common-cp',
                  on:{
                    click:(e)=>{
                      e.stopPropagation();
                      this.showInventoryModal(params.index);
                    }
                  }
                },params.row.currentInventory),
              ])
            }
          },
          {
            title: '出库数量',
            align: 'center',
            slot: 'number',
            key: 'number',
            renderHeader:(h,params)=>{
              return h('div',[
                h('span', {
                  class:'zd-cm-required'
                },'出库数量'),
              ])
            }
          },
          /*{
            title: '价格',
            align: 'center',
            slot: 'price',
            key: 'price',
            renderHeader:(h,params)=>{
              return h('div',[
                h('span', {
                  class:'zd-cm-required'
                },'价格'),
              ])
            }
          },*/
          /*{
            title: '购买人',
            align: 'center',
            slot: 'purchaser',
          },*/
          {
            title: '金额',
            align: 'center',
            slot: 'amount',
            key: 'amount',
          }
        ],
        formGoodsData:{
          attrData:[]
        },
        provideRemarkForm:{
          remark:''
        },
        ruleValidate: {
          date: [
            { required: true, type:'date', message:'不能为空', trigger: 'change' }
          ],
          warehouse: [
            { required: true, message:'不能为空', trigger: 'change' }
          ],
          type: [
            { required: true, message:'不能为空', trigger: 'change' }
          ],
        },
        saveLoading:false,
        leaderArr:[],
        loading:false,
        date:'',
        userName:'',
        addGoodsIndex:null,
        addGoodsId:null,
        //添加物品
        addGoodsModal:false,
        delId:'',
        delIndex:'',
        //通过code添加
        code:'',
        //修改
        editId:this.$route.params.id,
        editOrderNO:'',
        //库存
        goodsName:'',
        outNumber:null,
        inventoryModal:false,
        inventoryCols:[
          {
            type: 'index',
            title:'序号',
            align: 'center',
            width:65,
            key:'sortNum'
          },
          {
            title: '入库日期',
            align: 'center',
            key: 'date',
            width:110
          },
          {
            title: '入库单号',
            align: 'center',
            key: 'orderNO',
            width:140
          },
          {
            title: '入库价格',
            align: 'center',
            key: 'price',
            width:100
          },
          {
            title: '剩余数量',
            align: 'center',
            key: 'surplusNum',
            width:100
          },
          {
            title: '本次出库数量',
            align: 'center',
            slot: 'outNum',
            key: 'outNum',
          },
          {
            title: '金额',
            align: 'center',
            slot: 'amount',
            key: 'amount',
          }
        ],
        inventoryData:{
          attrData:[]
        },
        tmpInventory:[],
        subInvArr:[]
      }
    },
    computed:{

    },
    created(){
      this.getOutKindList().then((response)=>{
        this.typeArr = response.data;
        this.getOutWarehouseRecordInfo(this.editId);
      });
      this.getDepartment().then((response)=>{
        this.departmentArr = response.data;
      });
      const e = new Date();
      this.date = e.getFullYear() +"-"+ this.fillZero(e.getMonth()+1) +"-"+ this.fillZero(e.getDate());
      this.$store.dispatch('getBaseInfo').then((res) => {
        this.remoteMethod(res.userName);
      });
    },
    methods: {
      //修改获取详情
      getOutWarehouseRecordInfo(id) {
        BaseAxios({
          method: 'post',
          url: ' /sr/storeroomData!getOutWarehouseRecordInfo.action',
          data:{
            id:id
          },
        })
          .then((response) => {
            let aData = response.data;
            if(Object.keys(aData).length>0){
              this.provideForm.date = new Date(aData.date);
              this.provideForm.warehouse = aData.warehouseId;
              this.provideForm.warehouseName = aData.warehouseName;
              this.provideForm.type = aData.kindCode;
              this.provideForm.department = aData.departmentId;
              this.provideForm.leader = aData.buyserId;
              this.provideRemarkForm.remark = aData.note;
              aData.goodsList.map((item)=>{
                this.formGoodsData.attrData.push(
                  {
                    id:item.goodsInfoId,
                    name:item.goodsInfoName,
                    code:item.code,
                    classify:item.goodsKindName,
                    brand:item.brand,
                    model:item.size,
                    unit:item.unit,
                    number:item.goodsNum,
                    currentInventory:item.inventory,
                    amount:item.totalPrice,
                  }
                )
              })
              this.editOrderNO = aData.num;
              this.editId = aData.id;
              this.userName = aData.userName;
              this.date = aData.productDate;
            }
          })
          .catch((error) => {
            this.postErrorTip('请求失败');
            console.log(error);
          });
      },
      //日期
      changeDate(val){
        this.provideForm.date = val;
      },
      //回到出库单列表
      toOut(){
        this.$router.push({path:'/out'});
      },
      //合计
      handleSummary ({ columns, data }) {
        const sums = {};
        columns.forEach((column, index) => {
          const key = column.key;
          if (index === 0) {
            sums[key] = {
              key,
              value: '合计'
            };
            return;
          }
          const values = data.map(item => Number(item[key]));
          if (!values.every(value => isNaN(value)) && (index===8 || index===9)) {
            const v = values.reduce((prev, curr) => {
              const value = Number(curr);
              if (!isNaN(value)) {
                return prev + curr;
              } else {
                return prev;
              }
            }, 0);
            sums[key] = {
              key,
              value: v
            };
          } else {
            sums[key] = {
              key,
              value: ''
            };
          }
        });
        return sums;
      },
      //选择领用人
      remoteMethod(name){
        this.selectLeader(name).then((res)=>{
          this.leaderArr = res.data;
        })
      },
      //添加物品
      addGoodsHandle(){
        if(this.provideForm.warehouse === ""){
          this.postWarningTip('请先选择出库仓库');
          return
        }
        this.addGoodsModal=true;
        let addGood = this.formGoodsData.attrData;
        let addGoodId = [];
        addGood.map((item)=>{
          addGoodId.push(item.id);
        });
        this.$refs.child.getKind('1');
        this.$refs.child.selectedArr = addGood.slice();
        this.$refs.child.selectedArrId = addGoodId;
      },
      saveGoodsHandle(){
        this.addGoodsModal = false;
        this.formGoodsData.attrData = this.$refs.child.selectedArr.slice();
        this.getInventoryByParam(this.$refs.child.selectedArrId.join(','),this.provideForm.warehouse);
      },
      getCurrIndex(index){
        let flag = false;
        let invArr = [];
        this.tmpInventory.map((item)=>{
          if(item.id === this.formGoodsData.attrData[index].id){
            for (let i = 0; i < item.arr.length; i++) {
              if(item.arr[i].outNum!==null){
                invArr = item.arr;
                flag = true;
                break
              }
            }
          }
        });
        if(flag){
          let tmpInvAmount = null;
          invArr.map((item)=>{
            if(item.outNum!==null){
              tmpInvAmount += item.amount
            }
          });

          this.formGoodsData.attrData[index].amount = tmpInvAmount;
        }else{
          this.getGoodsList(this.formGoodsData.attrData[index].id,this.provideForm.warehouse).then((response)=>{
            let aData = response.data;
            let tmpCount = null;
            let tmpNumber = this.formGoodsData.attrData[index].number;
            if(tmpNumber!==null){
              for (let i = 0; i < aData.length; i++) {
                if(aData[i].inventory >= tmpNumber){
                  tmpCount += parseFloat(aData[i].unitPrice)*tmpNumber;
                  break
                }else{
                  tmpNumber -= aData[i].inventory;
                  tmpCount += parseFloat(aData[i].unitPrice)*aData[i].inventory;
                }
              }
              this.formGoodsData.attrData[index].amount = tmpCount;
            }else{
              this.formGoodsData.attrData[index].amount = null;
            }
          })
        }
      },
      //通过code添加物品
      getGoodsByCodeHandle(){
        if(this.code === ""){
          this.postWarningTip('请输入物品编码进行添加');
          return
        }
        this.getGoodsByCode(this.code);
      },
      getGoodsByCode(code){
        const ids = this.formGoodsData.attrData.map((item)=>{
          return item.id;
        })
        this.getGoodsInfoByUseType('1','','',code).then((res)=>{
          let aData = res.data;
          aData.map((item)=>{
            if(ids.indexOf(item.id)<0){
              this.formGoodsData.attrData.push(
                {
                  id:item.id,
                  name:item.name,
                  code:item.code,
                  classify:item.goodsKind,
                  brand:item.brand,
                  model:item.size,
                  unit:item.unit,
                  number:null,
                  currentInventory:null,
                  price:null,
                  amount:null,
                  priceList:[]
                }
              )
              this.getInventoryByParam(item.id,this.provideForm.warehouse);
            }
          })
          this.code = "";

        });
      },
      //根据物品仓库获取库存
      getInventoryByParam(goodsInfoIds,warehouseId) {
        BaseAxios({
          method: 'post',
          url: '/sr/storeroomData!getInventoryByParam.action',
          data:{
            goodsInfoIds:goodsInfoIds,
            warehouseId:warehouseId
          },
        })
          .then((response) => {
            let aData = response.data;
            for (let key in aData){
              this.formGoodsData.attrData.map((item)=>{
                if(item.id === key){
                  this.$set(item,'currentInventory',aData[key].inventory);
                }
              })
            }
          })
          .catch((error) => {
            this.postErrorTip('请求失败');
            console.log(error);
          });
      },
      //根据物品id获取库存列表
      getGoodsListInWarehouse(goodsInfoId,warehouseId) {
        this.getGoodsList(goodsInfoId,warehouseId).then((response)=>{
          this.inventoryData.attrData = [];
          let aData = response.data;
          if(this.outNumber !== null){
            let totalOutNumber = this.outNumber;
            for (let i = 0; i < aData.length; i++) {
              let outNum = null;
              let item = aData[i];
              if(item.inventory>=totalOutNumber){
                outNum = totalOutNumber;
                totalOutNumber = 0;
              }else{
                outNum = item.inventory;
                totalOutNumber -= item.inventory;
              }
              this.inventoryData.attrData.push({
                id:item.recordGoodsId,
                recordKind:item.recordKind,
                date:item.date,
                orderNO:item.num,
                price:item.unitPrice,
                surplusNum:item.inventory,
                outNum:outNum === 0 ? null : outNum,
                amount:(outNum !== 0 && outNum !== null) ? (item.unitPrice*outNum) : null
              })
            }
          }else{
            aData.map((item)=>{
              this.inventoryData.attrData.push(
                {
                  id:item.recordGoodsId,
                  recordKind:item.recordKind,
                  date:item.date,
                  orderNO:item.num,
                  price:item.unitPrice,
                  surplusNum:item.inventory,
                  outNum:item.outNum === 0 ? null : item.outNum,
                  amount:null
                }
              )
            })
          }
        })
      },
      getGoodsList(goodsInfoId,warehouseId) {
        return new Promise((resolve, reject) => {
          BaseAxios({
            method:'post',
            url:'/sr/storeroomData!getOutWarehouseRecordDetail.action',
            data:{
              goodsInfoId:goodsInfoId,
              warehouseId:warehouseId,
              recordId:this.editId
            }
          })
            .then((response) => {
              resolve(response)
            })
        }).catch(err => {
          reject(err)
        })
      },
      //显示库存
      showInventoryModal(index){
        let isRequest = false;
        let aArr = [];
        this.addGoodsIndex = index;
        this.inventoryModal = true;
        this.addGoodsId = this.formGoodsData.attrData[index].id;
        this.goodsName = this.formGoodsData.attrData[index].name;
        this.outNumber = this.formGoodsData.attrData[index].number;

        if(this.outNumber === null){
          this.tmpInventory.map((item,index)=>{
            if(item.id === this.addGoodsId){
              this.tmpInventory.splice(index,1);
            }
          })
        }else{
          let item = JSON.parse(JSON.stringify(this.tmpInventory));
          for (let i = 0; i < item.length; i++) {
            if(item[i].id === this.addGoodsId){
              isRequest = true;
              aArr = item[i].arr;
              break;
            }
          }
        }
        if(isRequest){
          this.inventoryData.attrData = aArr;
        }else{
          this.getGoodsListInWarehouse(this.formGoodsData.attrData[index].id,this.provideForm.warehouse);
        }

      },
      //显示库存列表合计
      handleInventorySummary({ columns, data }) {
        const sums = {};
        columns.forEach((column, index) => {
          const key = column.key;
          if (index === 0) {
            sums[key] = {
              key,
              value: '合计'
            };
            return;
          }

          const values = data.map(item => Number(item[key]));
          if (!values.every((value) => isNaN(value)) && (index===4 || index===5 || index===6)) {
            const v = values.reduce((prev, curr) => {
              const value = Number(curr);
              if (!isNaN(value)) {
                return prev + curr;
              } else {
                return prev;
              }
            }, 0);
            sums[key] = {
              key,
              value: v
            };
          } else {
            sums[key] = {
              key,
              value: ''
            };
          }
        });
        return sums;
      },
      //隐藏库存列表
      cancelInventoryModal(){
        this.inventoryModal = false;
        this.$refs['inventoryData'].resetFields();
      },
      //重新选择库存数量
      confirmInventoryModal(){
        let selArr = this.inventoryData.attrData;
        let flag = false;
        for (let i = 0; i < selArr.length; i++) {
          if(selArr[i].outNum !== null){
            flag = true;
            break;
          }
        }
        if(flag){
          this.tmpInventory.map((item,index)=>{
             if (item.id === this.addGoodsId){
               this.tmpInventory.splice(index,1);
             }
          });
          this.tmpInventory.push({'id':this.addGoodsId,'arr':selArr});
          let totalOutNum = null;
          selArr.map((item)=>{
            totalOutNum += item.outNum;
          });
          this.formGoodsData.attrData[this.addGoodsIndex].number = totalOutNum;
          //this.$refs['formGoodsData'].validate();
        }else{
          this.formGoodsData.attrData[this.addGoodsIndex].number = null;
          this.tmpInventory.map((item,index)=>{
            if(item.id === this.addGoodsId){
              this.tmpInventory.splice(index,1);
            }
          })
        }
        this.getCurrIndex(this.addGoodsIndex);
        this.inventoryModal = false;
      },
      //计算库存总金额
      calcAmount(index){
        this.inventoryData.attrData[index].amount = this.inventoryData.attrData[index].price*this.inventoryData.attrData[index].outNum;
      },

      //保存
      saveOrUpdateIntoWarehouseRecord(date,warehouse,type,department,buyer,note,goodsArr,code,id) {
        let flag1 = true,flag2=true;
        let params = {};
        this.subInvArr = [];
        params['warehouseRecord.date'] = date;
        params['warehouseRecord.warehouse.id'] = warehouse;
        params['warehouseRecord.outKind'] = type;
        if(department!==""){
          params['warehouseRecord.department.id'] = department;
        }
        if(buyer!==""){
          params['warehouseRecord.buyer.id'] = buyer;
        }
        params['warehouseRecord.note'] = note;
        params['warehouseRecord.code'] = code;
        params['warehouseRecord.id'] = id;

        if(this.tmpInventory.length>0){
          flag1 = false;
          this.tmpInventory.map((tmpItem)=>{
            goodsArr.map((item,index)=> {
              if (item.id === tmpItem.id) {
                tmpItem.arr.map((child) => {
                  if(child.outNum !== null) {
                    this.subInvArr.push({
                      id: child.id,
                      recordKind: child.recordKind,
                      outNum: child.outNum
                    });
                  }
                })
              }else{
                this.getGoodsList(item.id,this.provideForm.warehouse).then((response)=>{
                  let aData = response.data;
                  if(item.number !== null){
                    let totalOutNumber = item.number;
                    aData.map((item)=>{
                      let outNum = null;
                      if(item.inventory>=totalOutNumber){
                        outNum = totalOutNumber;
                        totalOutNumber = 0;
                      }else{
                        outNum = item.inventory;
                        totalOutNumber -= item.inventory;
                      }
                      this.subInvArr.push({
                        id:item.recordGoodsId,
                        recordKind:item.recordKind,
                        outNum:outNum === 0 ? null : outNum
                      })
                    })
                  }
                  this.subInvArr.map((item,i)=>{
                    if(item.outNum!==null){
                      params['outGoodsVOs['+i+'].recordGoodsId'] = item.id;
                      params['outGoodsVOs['+i+'].count'] = item.outNum;
                      params['outGoodsVOs['+i+'].recordKind'] = item.recordKind;
                    }
                  });

                })
              }
              if(index === (goodsArr.length -1)){
                  flag1 = true;
              }
            })
          })
        }else{
          flag2 = false;
          goodsArr.map((item,index)=>{
            this.getGoodsList(item.id,this.provideForm.warehouse).then((response)=>{
              let aData = response.data;
              if(item.number !== null){
                let totalOutNumber = item.number;
                aData.map((item)=>{
                  let outNum = null;
                  if(item.inventory>=totalOutNumber){
                    outNum = totalOutNumber;
                    totalOutNumber = 0;
                  }else{
                    outNum = item.inventory;
                    totalOutNumber -= item.inventory;
                  }
                  this.subInvArr.push({
                    id:item.recordGoodsId,
                    recordKind:item.recordKind,
                    outNum:outNum === 0 ? null : outNum
                  })
                })
              }
              this.subInvArr.map((item,i)=>{
                if(item.outNum!==null){
                  params['outGoodsVOs['+i+'].recordGoodsId'] = item.id;
                  params['outGoodsVOs['+i+'].count'] = item.outNum;
                  params['outGoodsVOs['+i+'].recordKind'] = item.recordKind;
                }
              });
            })
            if(index === (goodsArr.length -1)){
              flag2 = true;
            }
          });
        }
        if(flag1 && flag2){
          setTimeout(()=>{
            BaseAxios({
              method: 'post',
              url: '/sr/storeroomData!saveOrUpdateOutWarehouseRecord.action',
              data:params,
            })
              .then((response) => {
                this.saveLoading = false;
                this.toOut();
              })
              .catch((error) => {
                this.saveLoading = false;
                this.postErrorTip('请求失败');
                console.log(error);
              });
          },500)
        }

      },
      //保存验证
      handleSubmit(){
        let flag = false,flag2 = false;
        this.$refs['provideForm'].validate((valid) => {
          if (valid) {
            flag = true;
          }
        });
        if(this.formGoodsData.attrData.length<=0){
          this.postWarningTip('请添加物品');
        }else{
          this.$refs['formGoodsData'].validate((valid) => {
            if(valid){
              flag2 = true;
            }
          });
          if(flag && flag2){
            this.saveLoading = true;
            const e = new Date(this.provideForm.date);
            const date = e.getFullYear() +"-"+ this.fillZero((e.getMonth()+1)) +"-"+ this.fillZero(e.getDate());
            this.saveOrUpdateIntoWarehouseRecord(date,this.provideForm.warehouse,this.provideForm.type,this.provideForm.department,this.provideForm.leader,this.provideRemarkForm.remark,this.formGoodsData.attrData,this.editOrderNO,this.editId);
          }
        }
      },

      //删除添加的物品
      clickRowHandle(row,index){
        window.event.stopPropagation();
        if(this.delId === row.id){
          this.delId = "";
          this.delIndex = "";
        }else{
          this.delId = row.id;
          this.delIndex = index;
        }
      },
      rowClassName(row, index) {
        if (this.delId === row.id) {
          return 'demo-table-info-row';
        }
        return '';
      },
      delRowHandle(){
        if(this.delIndex === ""){
          return this.postWarningTip('请选择删除的物品');
        }else{
          this.formGoodsData.attrData.splice(this.delIndex,1);
          if(this.$refs.child.selectedArrId.indexOf(this.delId)>-1){
            let index = this.$refs.child.selectedArrId.indexOf(this.delId);
            this.$refs.child.selectedArr.splice(index,1);
            this.$refs.child.selectedArrId.splice(index,1);
          }
        }
      }
    },
    mounted() {

    }
  }
</script>
<style lang="less" scoped>
  .ivu-table .demo-table-info-row td{
    background-color: rgba(28, 144, 243, 0.1);
  }
</style>



